import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanUserModel } from 'src/models/africlean-user.model';
import { AfricleanAlertProvider } from 'src/providers/africlean-alert/africlean-alert.provider';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  public user: AfricleanUserModel;

  constructor(private navCtrl: NavController,
              private session: AfricleanSessionProvider,
              private alert: AfricleanAlertProvider) {}

  /**
   * Initialisation de la page
   */
  ngOnInit(): void {
    this.getUser();
  }

  /**
   * RÃ©cupÃ¨re l'utilisateur courant
   */
  private async getUser() {
    this.user = await this.session.getItem('user');
  }

  /**
   * Permet d'Ãªtre redirigÃ© sur la page de la fonctionnalitÃ© sÃ©lectionnÃ©e
   * @param page : string
   */
  public async goToPage(page: string) {
    this.isSubscribe(page);
  }

  /**
   * Teste si c'est un client et donc un abonnÃ©
   * @param page page
   */
  private async isSubscribe(page: string) {

    const role = await this.session.getItem('role');

    if (role === 'CLIENT') {

      const isSubscribe = await this.session.getItem('isSubscribe');

      if (page !== 'consult-abonnement' && page !== 'consult-informations') {

        if (isSubscribe === 'NULL' || isSubscribe === 'RESILIEE'  || isSubscribe === 'EN COURS') {

          this.onDemandToSubscribe();
          return;

        }
      }
    }
    this.navCtrl.navigateForward(page);
  }

  /**
   * Demande de s'abonner
   */
  public onDemandToSubscribe() {
    const message = 'Pour accÃ¨der Ã  cette fonctionnalitÃ©, veuillez vous abonner.';
    const css = '';
    const buttons = [
        {
          text: 'S\'abonner',
          handler: () => {
            this.navCtrl.navigateForward('consult-abonnement');
          }
        }
    ];
    this.alert.alertMessage(message, css, buttons);
  }

}
